.modal-role {
    display: flex;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.4);
    justify-content: center;
    position: fixed;
    opacity: 0;
    visibility: hidden;
    z-index: 1000;
  }
  .modal-role.open {
    display: flex;
    opacity: 1;
    visibility: visible;
  }
  .modal-child-role {
    background-color: #fff;
    width: 900px;
    min-height: 200px;
    position: relative;
    max-width: calc(100%-32px);
  }
  .modal-role-headerr {
    background-color: #a2041b;
    height: 65px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 25px;
    color: #fff;
  }
  .modal-role-close {
    position: absolute;
    right: 0;
    top: 0;
    color: #fff;
    padding: 12px;
    font-size: 30px;
  }
  .modal-role-close:hover {
    cursor: pointer;
    opacity: 0.6;
  }
  .modal-role-body {
    padding: 16px;
  }
  .modal-role-label {
    display: block;
    font-size: 20px;
    margin-bottom: 12px;
    font-weight: 500;
  }
  
  .modal-role-input {
    border: solid #ccc;
    border-radius: 7px;
    width: 100%;
    padding: 10px;
    font-size: 15px;
    margin-bottom: 12px;
  }
  #create-role {
    background-color: #a2041b;
    color: #fff;
    border: none;
    width: 100%;
    font-size: 30px;
    text-transform: uppercase;
    padding: 18px;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  
  #create-role:hover{
    opacity: 0.6;
  
  }

