.col-10 {
  position: relative;
  left: 320px;
}
.stone {
  height: 1200px;
  width: 1200px;
  background-color: #fff4e8;
  position: absolute;
  margin-left: -47px;
}

.tagResultStone {
    position: absolute;
    left: 20px;
    height: 100px;
    width: 330.2px;
    background-color: #fff4e8;
    border-radius: 7px;
    box-shadow: 0 4px 5px 5px rgba(0, 0, 0, 0.1);
}
.tagChild{
    position: absolute;
    top: 8px;
    left: 8px;
    width: 80px;
    height: 85px;
    border-radius: 7px;
    background-color: #A2041b;
}
.tagResultStone h3{
    position: absolute;
    font-size: 24px;
    left: 100px;
    top: 13px;

  }


  .tagResultStone p{
    position: absolute;
    font-weight: 570;
    font-size: 16px;
    top: 50px;
    left: 108px;
  }
  .stoneDetail p{
    position: absolute;
    font-weight: 500;
    font-size: 18px;
    top: 15px;
    left: 20px;
    
  }
 
  .addStone{

    position: absolute;
    top: 48px;
    left: 262px;
    height: 30px;
    width: 110px;
    border-radius: 7px;
    background-color: #9df99d;
    border: none;
  }
  .addStone i{
    position: absolute;
    top: 8px;
    left: 8px;

}
.addStone p{
    position: absolute;
    font-size: 13px;
    top: 7px;
    left: 28px;
    font-weight: 500;
}
.stoneDetail{
    position: absolute;
    width: 95%;
    height: 700px;
    background-color: #fff4e8;
    border-radius: 7px;
    box-shadow: 0 4px 5px 5px rgba(0, 0, 0, 0.1);
    top: 210px;
    left: 20px;
  }
  .modal-stone {
    display: flex;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.4);
    justify-content: center;
    position: fixed;
    opacity: 0;
    visibility: hidden;
    z-index: 1000;
  }
  
  .modal-stone.open {
    display: flex;
    opacity: 1;
    visibility: visible;
  }
  
  .modal-stone-child {
    background-color: #fff;
    width: 900px;
    min-height: 200px;
    position: relative;
    max-width: calc(100%-32px);
  }
  
  .modal-stone-headerr {
    background-color: #a2041b;
    height: 65px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 25px;
    color: #fff;
  }
  
  .modal-stone-close {
    position: absolute;
    right: 0;
    top: 0;
    color: #fff;
    padding: 12px;
    font-size: 30px;
  }
  
  .modal-stone-close:hover {
    cursor: pointer;
    opacity: 0.6;
  }
  
  .modal-stone-body {
    padding: 16px;
  }
  
  .modal-stone-label {
    display: block;
    font-size: 20px;
    margin-bottom: 12px;
    font-weight: 500;
  }
  
  .modal-stone-input {
    border: solid #ccc;
    border-radius: 7px;
    width: 100%;
    padding: 10px;
    font-size: 15px;
    margin-bottom: 12px;
  }
  
  #create-stone {
    background-color: #a2041b;
    color: #fff;
    border: none;
    width: 100%;
    font-size: 30px;
    text-transform: uppercase;
    padding: 18px;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  
  #create-stone:hover {
    opacity: 0.6;
  }


  .modal-stone-update {
